<template>
    <div class="body">
      <h1>英语考试题</h1>
      <ul>
        <li v-for="(item, index) in list" :key="index" @click="showInfo(item)">
          <a>{{ item.title }}</a>
        </li>
      </ul>
    </div>
</template>

<script>
export default {
  name: 'english',
  data () {
    return {
      list: [
        {title: 'E1'},
        {title: 'E2'},
        {title: 'E3'},
        {title: 'E4'},
        {title: 'E5'},
        {title: 'E6'},
        {title: 'E7'},
        {title: 'E8'},
        {title: 'E9'},
        {title: 'E10'}
      ]
    }
  },
  methods: {
    showInfo: function (item) {
      this.$router.push(`/english-info/${item.title}`)
    }
  }
}
</script>

<style scoped>
h1 {
  text-align: center;
}
  ul, li {
    list-style: none;
  }
  li {
    border-bottom: 1px solid gainsboro;
  }
  li a {
    padding: 10px 20px;
    display: inline-block;
    color: dodgerblue;
  }
</style>



